* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100vw;
  height: 100vh;
  overflow-y: scroll;
  overflow-x: hidden;
}

header {
  background-color: #212121;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
}

.header-background {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
  opacity: 0.5;
}

nav {
  width: 80%;
  background-color: black;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  border-radius: 0.5rem;
}

nav.sticky {
  position: fixed;
  top: 1rem;
  z-index: 99;
}

.nav-button {
  padding: 1rem;
  font-size: 1.5rem;
  color: white;
  border: none;
  background-color: transparent;
  cursor: pointer;
  font-family: "Poppins";
}

.nav-button:hover {
  border-bottom: 1px solid white;
}

.home {
  font-family: "Poppins";
  color: white;
  height: 130vh;
  width: 100%;
  padding: 1rem 2rem;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_18___});
  background-size: cover;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.home > .left-side {
  margin-top: 30%;
  padding: 1rem;
}

.headline {
  font-size: 3rem;
  font-weight: bold;
}

.home-paragraph {
  font-size: 1.2rem;
}

.home-buttons {
  margin-top: 1rem;
}

.order-button {
  padding: 1rem 1.5rem;
  border: none;
  background-color: #fd930b;
  color: white;
  font-weight: bold;
  font-family: "Poppins";
  cursor: pointer;
}

.explore-button {
  padding: 1rem 1.5rem;
  border: 1.5px white solid;
  background-color: transparent;
  font-weight: bold;
  color: white;
  font-family: "Poppins";
  margin-left: 1rem;
  cursor: pointer;
}

.explore-button:hover {
  color: black;
  background-color: white;
}

.menu {
  height: 100%;
  width: 100%;
  background-color: #313131;
  padding: 0 2rem;
  padding-top: 10%;
  color: white;
  font-family: "Poppins";
}

.menu-list {
  width: 100%;
  padding: 3rem;
  background-color: #212121;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  grid-template-rows: repeat(auto-fill, minmax(400px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.menu-image {
  width: 100%;
  height: 70%;
  border-radius: 1rem;
}

.menu-headline {
  width: 100%;
  text-align: center;
  font-size: 2.3rem;
}

.flex-order {
  display: flex;
  justify-content: space-between;
  padding: 1rem 3rem;
  align-items: center;
}

.menu-name {
  font-size: 1.5rem;
}

.flex-order > .order-button {
  border: 1px white solid;
  background-color: transparent;
  border-radius: 0.5rem;
  color: #ffffff;
}

.flex-order > .order-button:hover {
  background-color: white;
  color: black;
}

.about {
  height: 100%;
  width: 100%;
  background-color: #262932;
  padding: 1rem 2rem;
  padding-top: 3rem;
  color: white;
  font-family: "Poppins";
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 3rem;
}

.contact-us {
  grid-column: 1/3;
  justify-self: center;
  padding: 0.5rem 1rem;
  background-color: white;
  width: 60%;
  height: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  border-radius: 1rem;
  margin-top: 6rem;
}

.contact-us > button {
  padding: 1rem 2rem;
  border-radius: 1rem;
  background-color: #fd930b;
  color: white;
  border: none;
  font-weight: bold;
}

#contact-us {
  width: 80%;
  height: 90%;
  padding: 1rem;
  font-size: 1.4rem;
  border: none;
}

.about-us {
  height: 40vh;
  padding: 1rem 4rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.gi-touch {
  height: 40vh;
  justify-self: center;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 1rem 4rem;
}

.email-input {
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 2rem;
}

#email {
  padding: 1rem;
  border-radius: 0.5rem;
}

.email-input > button {
  padding: 1rem 1.5rem;
  font-size: 1rem;
  border-radius: 0.5rem;
  background-color: #fd930b;
  color: white;
  border: none;
  font-weight: bold;
}

footer {
  background-color: #262932;
  width: 100%;
  height: 10%;
  color: white;
  font-family: "Poppins";
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-top: 1px solid white;
